.nav {
  padding-top: 20px;

  display: flex;
  justify-content: space-between;

  &__list {
    margin: 0;
    padding: 0;

    display: flex;
    gap: 40px;
    align-items: center;

    list-style: none;
  }

  &__link {
    font-weight: 500;
    font-size: 15px;

    text-decoration: none;

    color: $color-primary-200;

    &:hover {
      color: $color-primary-300;
      text-decoration: none;
      transition: color 0.3s linear;

      .nav__social {
        fill: $color-primary-300;
        transition: fill 0.3s linear;
      }
    }
  }

  &__socials {
    display: flex;
    gap: 30px;
  }
}
